<template lang="pug">
  .qr-code-wrapper
    .frow.centered-column.justify-around.height-100.temporary-pink
      div
        .header My ChaserID
        .header-text Show this at the door to get scanned in!
        VueQrcode(
          :value="qrcode"
          tag="img"
          :options="{ width: 300 }"
        )
      VButton(kind="outline-white" text="Done" @click.native="$router.push({name: 'home'})")
      div
        | PS: Screenshots don't work.
        | {{ qrcode }}
</template>

<script>
import VueQrcode from '@chenfengyuan/vue-qrcode';
export default {
  name: 'ChaserID',
  components: {
    VueQrcode,
  },
  data() {
    return {
      now: null,
    };
  },
  computed: {
    qrcode() {
      return `${this.$store.getters['auth/user'].uid}:${this.now}`;
    },
  },
  beforeCreate() {
    document.body.classList.add('colored-page');
  },
  created() {
    this.now = this.moment().format('YYYY-MM-DDTHH:mm:ssZ');
    setInterval(() => {
      this.now = this.moment().format('YYYY-MM-DDTHH:mm:ssZ');
    }, 1000);
  },
  beforeDestroy: () => {
    document.body.classList.remove('colored-page');
  },
};
</script>

<style lang="sass" scoped>
.qr-code-wrapper
  text-align: center
  color: $white
  height: 100%
  width: 100%

.temporary-pink
  background-color: $primary-color

.header
  font-size: 34px
  margin-top: 30px
  font-weight: bold

.header-text
  text-align: center
  margin-top: 20px
  margin-left: 40px
  width: 80%

img
  border-radius: $border-radius-large
</style>
